.store-logos {
  background: linear-gradient(45deg, #ffffff 0%, #fbfbfb 95%);
  border-radius: 16px; 
  position: relative;
  overflow: hidden;
}

.store-logos .container {
  max-width: 1200px;
}

.logos-header {
  text-align: center;
  margin-bottom: 20px;
}

.logos-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
}

.logos-subtitle {
  font-size: 1.25rem;
  color: var(--text-secondary);
  max-width: 600px;
  margin: 0 auto;
}

.logos-carousel {
  position: relative;
  padding: 20px 0;
}

.logos-scroll-container {
  overflow-x: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
  scroll-behavior: smooth;
  padding: 10px 0;
  cursor: grab;
  -webkit-overflow-scrolling: touch; 
}

.logos-scroll-container::-webkit-scrollbar {
  display: none;
}

.logos-scroll-container:active {
  cursor: grabbing;
}

.logos-track {
  display: flex;
  gap: 24px;
  width: max-content;
  padding: 0 50px;
}

.logo-item {
  flex: 0 0 180px;
  transition: transform 0.3s;
}

.logo-item:hover {
  transform: translateY(-5px);
}

.logo-placeholder {
  background: white;
  padding: 32px 16px;
  border-radius: 24px;
  text-align: center;
  border: 2px dashed rgba(76, 175, 80, 0.3);
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.logo-placeholder:hover {
  border-color: var(--primary);
  box-shadow: 0 8px 16px rgba(76, 175, 80, 0.15);
  transform: scale(1.05);
}

.logo-icon {
  font-size: 3rem;
  margin-bottom: 16px;
  opacity: 0.5;
}

.logo-name {
  font-size: 1rem;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 16px;
}

.logo-badge {
  display: inline-block;
  background: rgba(76, 175, 80, 0.1);
  color: var(--primary-dark);
  font-size: 0.7rem;
  font-weight: 600;
  padding: 4px 10px;
  border-radius: 100px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.carousel-fade-left,
.carousel-fade-right {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100px;
  pointer-events: none;
  z-index: 2;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.carousel-fade-left.visible,
.carousel-fade-right.visible {
  opacity: 1;
}

.carousel-fade-left {
  left: 0;
  background: linear-gradient(to right, 
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 0.9) 30%,
    rgba(255, 255, 255, 0) 100%
  );
}

.carousel-fade-right {
  right: 0;
  background: linear-gradient(to left, 
    rgba(255, 255, 255, 1) 0%,
    rgba(255, 255, 255, 0.9) 30%,
    rgba(255, 255, 255, 0) 100%
  );
}

.logos-note {
  text-align: center;
  padding: 12px;
  border-radius: 16px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin: 16px 48px;
  width: auto;
  border: 1px solid rgba(76, 175, 80, 0.2);
  position: relative;
  z-index: 5;
  opacity: 0.5;
  transition: opacity 0.3s;
}

.logos-note:hover{
  opacity: 1;
}

.note-icon {
  font-size: 1.25rem;
}

.scroll-indicators {
  text-align: center;
  margin-bottom: 32px;
  min-height: 30px;
}

.indicator {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  opacity: 0.6;
  transition: opacity 0.3s;
  padding: 6px 16px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 50px;
  border: 1px solid rgba(76, 175, 80, 0.2);
  backdrop-filter: blur(5px);
}

.indicator:hover {
  opacity: 1;
}

.indicator-icon {
  font-size: 1.25rem;
}

.indicator-text {
  font-size: 0.875rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.desktop-indicator {
  display: inline-flex;
}

.mobile-indicator {
  display: none;
}

.desktop-indicator .indicator-icon {
  animation: dragHint 2s infinite;
}

@keyframes dragHint {
  0%, 100% {
    transform: translateX(-3px);
  }
  50% {
    transform: translateX(3px);
  }
}

@keyframes swipeHint {
  0%, 100% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(5px);
  }
}

.mobile-indicator .indicator-icon {
  animation: swipeHint 2s infinite;
}

.logos-scroll-container.dragging {
  cursor: grabbing !important;
}

.logos-scroll-container.dragging .logo-item {
  pointer-events: none; 
  transition: none;
}

@media (max-width: 768px) {
  .desktop-indicator {
    display: none;
  }
  
  .mobile-indicator {
    display: inline-flex;
  }
  
  .store-logos {
    padding: 60px 16px;
  }
  
  .logos-header h2 {
    font-size: 2.05rem;
  }
  
  .logo-item {
    flex: 0 0 140px;
  }
  
  .carousel-fade-left,
  .carousel-fade-right {
    width: 60px;
  }
  
  .swipe-indicator {
    display: flex;
  }
  
  .logos-note {
    flex-direction: column;
    text-align: center;
    padding: 16px;
    border-radius: 16px;
  }
}

@media (min-width: 769px) {
  .desktop-indicator {
    display: inline-flex;
  }
  
  .mobile-indicator {
    display: none;
  }
  
  .swipe-indicator {
    display: none; 
  }
}
