.pricing {
  background: linear-gradient(225deg, #ffffff 0%, #fbfbfb 95%);
  border-radius: 16px; 
  position: relative;
  overflow: hidden;
}

.pricing .container {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.section-badge {
  display: inline-block;
  background: rgba(76, 175, 80, 0.1);
  padding: 10px 20px;
  color: var(--primary-dark);
  font-weight: 600;
  font-size: 0.875rem;
  border-radius: 100px;
  margin-bottom: 32px;
}

.pricing-header {
  text-align: center;
  margin-bottom: 60px;
}

.pricing-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 20px;
}

.pricing-subtitle {
  font-size: 1.25rem;
  max-width: 600px;
  margin: 16px auto -12px;
}

.pricing-card {
  max-width: 500px;
  margin: 0 auto;
  background: white;
  border-radius: 48px;
  padding: 48px 24px;
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.04),
    inset 0 0 0 1px rgba(255, 255, 255, 0.8);
  position: relative;
  border: 2px solid rgba(76, 175, 80, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.pricing-card:hover {
  transform: translateY(-5px);
  box-shadow: 
    0 30px 60px rgba(76, 175, 80, 0.1),
    inset 0 0 0 1px rgba(255, 255, 255, 0.8);
}

.pricing-card-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.pricing-icon {
  font-size: 4rem;
  margin-bottom: 24px;
  background: rgba(76, 175, 80, 0.1);
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 2px solid rgba(76, 175, 80, 0.3);
}

.pricing-amount {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: 16px;
}

.currency {
  font-size: 2rem;
  font-weight: 600;
  color: var(--primary);
  margin-top: 10px;
}

.amount {
  font-size: 6rem;
  font-weight: 800;
  line-height: 1;
  color: var(--primary);
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-dark) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.pricing-description {
  font-size: 1.5rem;
  font-weight: 500;
  color: var(--text-secondary);
}

.pricing-divider {
  width: 100px;
  height: 2px;
  background: linear-gradient(90deg, transparent, rgba(76, 175, 80, 0.3), transparent);
  margin: 32px auto;
}

.pricing-note {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  font-size: 1.25rem;
  color: var(--text-primary);
  background: rgba(76, 175, 80, 0.05);
  padding: 16px 24px;
  border-radius: 50px;
  width: 100%;
  border: 1px solid rgba(76, 175, 80, 0.2);
}

.note-icon {
  color: var(--primary);
  font-size: 1.5rem;
  font-weight: bold;
}

.pricing-decoration {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 1;
}

@keyframes float {
  0% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(5deg);
  }
  100% {
    transform: translateY(0) rotate(0deg);
  }
}

@media (max-width: 768px) {
  .pricing-card {
    padding: 48px 24px;
  }

  .pricing-note {
    font-size: 1rem;
  }
  
  .pricing-header h2 {
    font-size: 2.05rem;
  }
}
