.faq {
  background: linear-gradient(225deg, #ffffff 0%, #fbfbfb 95%);
  border-radius: 16px; 
  position: relative;
  overflow: hidden;
}

.faq .container {
  max-width: 900px;
  margin: 0 auto;
}

.faq-header {
  text-align: center;
  margin-bottom: 40px;
}

.section-badge {
  display: inline-block;
  background: rgba(76, 175, 80, 0.1);
  padding: 10px 20px;
  color: var(--primary-dark);
  font-weight: 600;
  font-size: 0.875rem;
  border-radius: 100px;
  margin-bottom: 32px;
}

.faq-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
}

.faq-subtitle {
  font-size: 1.25rem;
  color: var(--text-secondary);
  max-width: 600px;
  margin: 0 auto;
}

.faq-grid {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 20px;
}

.faq-item {
  background: white;
  border-radius: 16px;
  border: 1px solid rgba(0, 0, 0, 0.08);
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.02);
}

.faq-item:hover {
  border-color: rgba(76, 175, 80, 0.3);
  box-shadow: 0 4px 12px rgba(76, 175, 80, 0.1);
  transform: translateY(-2px);
}

.faq-item.open {
  border-color: var(--primary);
  box-shadow: 0 8px 24px rgba(76, 175, 80, 0.15);
}

.faq-question {
  padding: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  font-weight: 600;
  color: var(--text-primary);
}

.question-text {
  font-size: 1.125rem;
  flex: 1;
}

.faq-icon {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: var(--primary);
  transition: transform 0.3s;
}

.faq-item.open .faq-icon {
  transform: rotate(180deg);
}

.faq-answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease;
  background: rgba(76, 175, 80, 0.02);
}

.faq-item.open .faq-answer {
  max-height: 200px;
}

.faq-answer p {
  padding: 0 24px 24px 24px;
  color: var(--text-secondary);
  line-height: 1.6;
}

.faq-footer {
  text-align: center;
  padding: 20px;
  background: white;
  border-radius: 16px;
  border: 1px solid rgba(76, 175, 80, 0.2);
}

.faq-footer p {
  font-size: 1.125rem;
  color: var(--text-secondary);
}

.faq-footer a {
  color: var(--primary);
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s;
}

.faq-footer a:hover {
  color: var(--primary-dark);
  text-decoration: underline;
}

@media (max-width: 768px) {
  .faq-header h2 {
    font-size: 2.05rem;
  }

  .faq-question {
    padding: 20px;
  }

  .question-text {
    font-size: 1rem;
  }

  .faq-answer p {
    padding: 0 20px 20px 20px;
    font-size: 0.95rem;
  }
}