.target-audience {
  background: linear-gradient(135deg, #ffffff 0%, #fbfbfb 95%);
  border-radius: 16px; 
  position: relative;
  overflow: hidden;
}

.target-audience .container {
  max-width: 1200px;
  position: relative;
  z-index: 2;
}

.audience-header {
  text-align: center;
  margin-bottom: 48px;
}

.audience-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
}

.audience-subtitle {
  font-size: 1.25rem;
  color: var(--text-secondary);
  max-width: 600px;
  margin: 0 auto;
}

.audience-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
  margin-bottom: 48px;
}

.audience-card {
  background: white;
  border-radius: 32px;
  overflow: hidden;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.03);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.audience-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 24px 56px rgba(76, 175, 80, 0.15);
  border-color: rgba(76, 175, 80, 0.3);
}

.card-image {
  position: relative;
  height: 200px;
  overflow: hidden;
}

.card-photo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.6s;
}

.audience-card:hover .card-photo {
  transform: scale(1.1);
}

.card-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, transparent 0%, rgba(0, 0, 0, 0.3) 100%);
}

.card-content {
  padding: 24px 24px;
  position: relative;
}

.card-icon {
  position: absolute;
  top: -25px;
  right: 24px;
  width: 50px;
  height: 50px;
  background: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.75rem;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.05);
  border: 2px solid var(--primary);
  z-index: 2;
}

.card-content h3 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 12px;
  color: var(--text-primary);
}

.card-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.tag {
  background: rgba(76, 175, 80, 0.1);
  color: var(--primary-dark);
  font-size: 0.75rem;
  font-weight: 600;
  padding: 4px 8px;
  border-radius: 100px;
  text-transform: uppercase;
  letter-spacing: 0.3px;
  border: 1px solid rgba(76, 175, 80, 0.2);
}

.card-primary .card-icon {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.card-secondary .card-icon {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  color: white;
}

.card-tertiary .card-icon {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
  color: white;
}

.audience-stats {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
  padding: 16px;
  background: white;
  border-radius: 50px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.03);
  border: 1px solid rgba(76, 175, 80, 0.2);
}

.stat-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.stat-number {
  font-size: 2rem;
  font-weight: 800;
  color: var(--primary);
  line-height: 1.2;
}

.stat-label {
  font-size: 0.875rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.stat-divider {
  width: 1px;
  height: 40px;
  background: rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
  .target-audience {
    padding: 60px 16px;
  }
  
  .audience-grid {
    grid-template-columns: 1fr;
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .audience-header h2 {
    font-size: 2.05rem;
  }
  
  .audience-subtitle {
    font-size: 1.25rem;
  }
  
  .audience-stats {
    flex-direction: column;
    gap: 20px;
  }
  
  .stat-item {
    width: 100%;
  }  
}